import React, { Fragment } from "react";
import { styled, useTheme } from "@mui/material/styles";
import List from "@mui/material/List";
import Divider from "@mui/material/Divider";
import IconButton from "@mui/material/IconButton";
import ChevronLeftIcon from "@mui/icons-material/ChevronLeft";
import ChevronRightIcon from "@mui/icons-material/ChevronRight";
import ListItem from "@mui/material/ListItem";
import ListItemButton from "@mui/material/ListItemButton";
import ListItemIcon from "@mui/material/ListItemIcon";
import ListItemText from "@mui/material/ListItemText";

import LogoAdmin from "./LogoAdmin";
import Option from "../_nav";
import { useHistory } from 'react-router-dom';
import AppSidebarItems from "./AppSidebarItems";

const DrawerHeader = styled("div")(({ theme }) => ({
  display: "flex",
  alignItems: "center",
  padding: theme.spacing(0, 1),
  // necessary for content to be below app bar
  ...theme.mixins.toolbar,
  justifyContent: "flex-end",
}));

const AppSidebar = ({ handleDrawerClose }) => {
  const theme = useTheme();
  const history = useHistory();

  const activeSection = (option) =>{
    for (let indexOption = 0; indexOption < Option.length; indexOption++) {
      if(option.name === Option[indexOption].name){
        Option[indexOption].active = true;
      }else{
        Option[indexOption].active = false;
      }
      if(Option[indexOption].items){
        for (let indexItem = 0; indexItem < Option[indexOption].items.length; indexItem++) {
          Option[indexOption].items[indexItem].active = false;
        }
      }
    }
  }
  
  return (
    <Fragment>
      <DrawerHeader sx={{background:"#035A40"}}>
        <LogoAdmin />
        <IconButton onClick={handleDrawerClose}>
          {theme.direction === "ltr" ? (
            <ChevronLeftIcon sx={{color:"#fff"}}/>
          ) : (
            <ChevronRightIcon sx={{color:"#fff"}}/>
          )}
        </IconButton>
      </DrawerHeader>
      <Divider sx={{background:"#035A40"}}/>
      <List sx={{background:"#035A40"}}>
        {Option.map((option, index) => ( 
          <div key={index}>
           
            {option.isGroup && (
              <ListItem key={index} >
                <ListItemText primary={option.name} />
              </ListItem>
            )}
            {!option.items && !option.isGroup && (
              <ListItemButton sx={{...(option.active && { background: "#053829" }) }} key={index} onClick={()=>{history.push(option.to);activeSection(option);}}>
                <ListItemIcon>{option.icon}</ListItemIcon>
                <ListItemText primary={option.name} />
              </ListItemButton>
            )}
            {option.items && !option.isGroup && (
              <AppSidebarItems key={index} option={option} activeSection={activeSection}/>
            )}

            
          </div>
        ))}
      </List>
    </Fragment>
  );
};

export default AppSidebar;
