import React, { useState, Fragment } from "react";
import List from '@mui/material/List';
import ListItemButton from "@mui/material/ListItemButton";
import ListItemIcon from "@mui/material/ListItemIcon";
import ListItemText from "@mui/material/ListItemText";
import Collapse from '@mui/material/Collapse';

import ExpandLess from '@mui/icons-material/ExpandLess';
import ExpandMore from '@mui/icons-material/ExpandMore';

import { useHistory } from 'react-router-dom';
import Option from "../_nav";
const AppSidebarItems = ({ option }) => {
  const [open, setOpen] = useState(false);
  const history = useHistory();

  const handleClick = () => {
    setOpen(!open);
  };

  const activeItemv2 = (item)=>{
    for (let indexOption = 0; indexOption < Option.length; indexOption++) {
      if(Option[indexOption].active){
        Option[indexOption].active=false;
      }
      if(Option[indexOption].items){
        for (let index = 0; index < Option[indexOption].items.length; index++) {
          if(Option[indexOption].items[index].name===item.name){
            Option[indexOption].items[index].active = true
          }else{
            Option[indexOption].items[index].active = false
          }
        }
      }
    }
    
  }
  return (
    <Fragment>
      <ListItemButton onClick={handleClick}>
        <ListItemIcon>{option.icon}</ListItemIcon>
        <ListItemText primary={option.name} />
        {open ? <ExpandLess /> : <ExpandMore />}
      </ListItemButton>
      <Collapse in={open} timeout="auto" unmountOnExit>
        <List component="div" disablePadding>
          {option.items.map((item, index) => (
            <Fragment>
              {item.active&&<ListItemButton style={{background:"#053829"}} key={index} sx={{ pl: 4 }} onClick={()=>{history.push(item.to);activeItemv2(item)}}>
              <ListItemText primary={item.name} />
            </ListItemButton>}
            {!item.active&&<ListItemButton  key={index} sx={{ pl: 4 }} onClick={()=>{history.push(item.to);activeItemv2(item)}}>
              <ListItemText primary={item.name} />
            </ListItemButton>}
            </Fragment>
          ))}
        </List>
      </Collapse>
    </Fragment>
  );
};

export default AppSidebarItems;
