import React, { useState, useEffect, useContext } from "react";
import { styled } from "@mui/material/styles";
import Box from "@mui/material/Box";
import Drawer from "@mui/material/Drawer";
import CssBaseline from "@mui/material/CssBaseline";
import MuiAppBar from "@mui/material/AppBar";
import Toolbar from "@mui/material/Toolbar";
import IconButton from "@mui/material/IconButton";
import MenuIcon from "@mui/icons-material/Menu";

import AppContent from "../views/admin/AppContent";
import UserContext from "../contex/user/UserContext";
import AppSidebar from "../components/AppSidebar";

import ViewCompactIcon from "@mui/icons-material/ViewCompact";
import { useHistory } from "react-router-dom";

import FullscreenIcon from "@mui/icons-material/Fullscreen";
import FullscreenExitIcon from "@mui/icons-material/FullscreenExit";

const drawerWidth = 310;

const Main = styled("main", { shouldForwardProp: (prop) => prop !== "open" })(
  ({ theme, open }) => ({
    flexGrow: 1,
    background: "#eeeeee",
    height: "100vh",
    padding: theme.spacing(3),
    transition: theme.transitions.create("margin", {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    marginLeft: `-${drawerWidth}px`,
    ...(open && {
      transition: theme.transitions.create("margin", {
        easing: theme.transitions.easing.easeOut,
        duration: theme.transitions.duration.enteringScreen,
      }),
      marginLeft: 0,
    }),
  })
);

const AppBar = styled(MuiAppBar, {
  shouldForwardProp: (prop) => prop !== "open",
})(({ theme, open }) => ({
  transition: theme.transitions.create(["margin", "width"], {
    easing: theme.transitions.easing.sharp,
    duration: theme.transitions.duration.leavingScreen,
  }),
  ...(open && {
    width: `calc(100% - ${drawerWidth}px)`,
    marginLeft: `${drawerWidth}px`,
    transition: theme.transitions.create(["margin", "width"], {
      easing: theme.transitions.easing.easeOut,
      duration: theme.transitions.duration.enteringScreen,
    }),
  }),
}));

export default function PersistentDrawerLeft() {
  const { setAdminArea } = useContext(UserContext);
  const [open, setOpen] = useState(true);
  const [screen, setScreen] = useState(false);
  const history = useHistory();

  const handleDrawerOpen = () => {
    setOpen(true);
  };

  const handleDrawerClose = () => {
    setOpen(false);
  };

  const rediret = () => {
    history.push("/");
  };

  const fullScreenAction = () =>{
    setScreen(true);
    document.documentElement.requestFullscreen();
  }

  const exitFullScreenAction = () =>{
    setScreen(false);
    document.exitFullscreen();
  }

  useEffect(() => {
    const initial = () => {
      setAdminArea(true);
    };
    initial();
  }, [setAdminArea]);
  return (
    <Box sx={{ display: "flex" }}>
      <CssBaseline />
      <AppBar position="fixed" open={open} sx={{ background: "#ffff" }}>
        <Toolbar>
          <IconButton
            color="inherit"
            aria-label="open drawer"
            onClick={handleDrawerOpen}
            edge="start"
            sx={{ mr: 2, ...(open && { display: "none" }) }}
          >
            <MenuIcon sx={{ color: "#636e72" }} />
          </IconButton>
          <IconButton onClick={rediret}>
            <ViewCompactIcon sx={{ fontSize: "35px" }} />
          </IconButton>
          {!screen && (
            <IconButton onClick={fullScreenAction}>
              <FullscreenIcon sx={{ fontSize: "35px" }}/>
            </IconButton>
          )}
          {screen && (
            <IconButton onClick={exitFullScreenAction}>
              <FullscreenExitIcon sx={{ fontSize: "35px" }}/>
            </IconButton>
          )}
        </Toolbar>
      </AppBar>
      <Drawer
        sx={{
          width: drawerWidth,
          flexShrink: 0,
          "& .MuiDrawer-paper": {
            width: drawerWidth,
            boxSizing: "border-box",
            background: "#035A40",
            color: "#dfe6e9",
          },
        }}
        variant="persistent"
        anchor="left"
        open={open}
      >
        <AppSidebar handleDrawerClose={handleDrawerClose} />
      </Drawer>
      <Main open={open}>
        <div style={{ height: "50px" }}></div>
        <AppContent />
      </Main>
    </Box>
  );
}
